.root {
    --dark-font-color: #fff;
    --dark-background: #444;
}

* {
    box-sizing: border-box;
}


html {
    font-size: 65.5%;
}

@media screen and (max-width: 755px) {
    html {
        font-size: 50%;
    }

}




body {
    font-family: 'Press Start 2P', cursive;
    font-size: 1.8rem;
    background-color: #333;
    color: #ccc;

}



/*typography*/

h1 {
    font-size: 5.2rem;

}

h2 {
    font-size: 4.4rem;
}

h3 {
    font-size: 3.6rem;
}

/*button*/

.btn {
    display: inline-block;
    outline: none;
    padding: 2rem 3rem;
    border: none;
    cursor: pointer;
}

.btn--white {
    background-color: #fff;
}

/*link*/

a:link,
a:visited {
    text-decoration: none;
    color: #555;

}

/*header*/
.header {
    display: grid;
    justify-content: center;
}

.header .btn {
    justify-self: center;
}

@media screen and (min-width: 655px) {
    .header {
        grid-template-columns: 1fr auto;
    }

    .header .btn {
        justify-self: left;
    }


}



/*hero*/

.hero-block {
    text-align: center;
}

.number__display {
    border-bottom: 10px solid #ccc;
}

.number {
    font-size: 4.4rem;
    background-color: #ccc;
    color: #333;
    width: 20rem;
    height: 15rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    transform: translateY(50%);

}

/*input-block*/

.input-block {
    margin-top: 15rem;
    display: grid;
}


.input {
    width: 30rem;
    height: 10rem;
    font-size: 4.4rem;
    text-align: center;
    outline: none;

}

.input__number__block {
    display: grid;
    justify-self: center;
    gap: 2rem;
    text-align: center;

}


.message {
    margin: 4rem 0;
}


.score__board {

    margin: 6rem 0;
    ;
    text-align: center;
}

@media screen and (min-width: 650px) {

    .input-block {

        grid-template-columns: 1fr 1fr;
    }

    .score__board {

        margin: 0;

    }

    .message {
        margin: 0;
    }


}

/*footer*/

.footer {
    text-align: center;
    margin-bottom: 6rem;
}

@media screen and (min-width: 650px) {
    .footer {
        display: none;
    }
}